import React from 'react';
import { InviteCreateNewBusinessElements } from './InviteCreateNewBusinessElements';
import EntityForm from '../../../modules/entity/forms';
import { useAppState } from '../../../shared/state';
import { useNotistack } from '../../../shared/hooks';

type Props = {
	onSuccess: () => void;
	parentOnSuccess: () => void;
	businessName: string;
};

export function InviteCreateNewBusinessForm({ onSuccess, businessName, parentOnSuccess }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	return (
		<>
			<div className="mb-4 text-center">
				<h2 className="text-lg font-bold">This Business has not been registered in KENNEKT.</h2>
				<h3 className="medium text-base">Would you like to add?</h3>
			</div>
			<EntityForm.Main
				entity="clients"
				name="AllClients"
				url={`/${companyId}/my/company/client_company/`}
				method="post"
				primaryKey="id"
				sendAsFormData={false}
				fields={[
					{
						name: 'email',
						required: true,
						type: 'email',
						onSubmitKey: ''
					},
					{
						name: 'first_name',
						required: true,
						onSubmitKey: ''
					},
					{
						name: 'last_name',
						required: true,
						onSubmitKey: ''
					},
					{
						name: 'mobile',
						required: true,
						onSubmitKey: ''
					},
					{
						name: 'name',
						required: true,
						value: businessName,
						onSubmitKey: ''
					}
				]}
				onSuccess={(response, resetForm): any => {
					showNotification({ message: 'Success! Waiting for client accepting..' });
					onSuccess();
					parentOnSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {}}
			>
				{({ isSubmitting, values, setFieldValue, resetForm }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('new business  create values', values);
					}
					return (
						<InviteCreateNewBusinessElements
							setFieldValue={setFieldValue}
							onSuccess={onSuccess}
							isSubmitting={isSubmitting}
						/>
					);
				}}
			</EntityForm.Main>
		</>
	);
}
